.example-wrapper {
  padding: 1em;
  border: 1px solid black;
  border-radius: 4px;
}

.example-wrapper__code {
  padding: 1em;
  font-size: 0.8em;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  background-color: #333;
  color: #fff;
  border-radius: 4px;
}

.example-wrapper__controls {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: baseline;
          align-items: baseline;
  margin-bottom: 1em;
}

.example-wrapper__button {
  min-width: 200px;
}

.example-wrapper__example {
  background: #eee;
  padding: 16px;
}
.react-fetcher-loading {
  padding: 1em;
  text-align: center;
}

.react-fetcher-spinner {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}

.react-fetcher-spinner__item {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 50px;
  height: 50px;
  margin: 6px;
  border: 6px solid;
  border-radius: 50%;
  -webkit-animation: react-fetcher-spinner-animation 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: react-fetcher-spinner-animation 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #777 transparent transparent transparent;
}
.react-fetcher-spinner__item:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.react-fetcher-spinner__item:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.react-fetcher-spinner__item:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}

@-webkit-keyframes react-fetcher-spinner-animation {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg);   }
  to   { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes react-fetcher-spinner-animation {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg);   }
  to   { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.react-fetcher-error {
  padding: 1em;
  text-align: center;
  line-height: 1.5;
}

.react-fetcher-success{
  padding: 1em;
  text-align: center;
}

.react-fetcher-preview {
  padding: 0.5em;
  border: 1px dashed #777;
  text-align: left;
  overflow: scroll;
  max-height: 400px;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.root {
  max-width: 600px;
  margin: 1.5em auto;
  padding: 1em;
  border: 1px solid #333;
  border-radius: 8px;
  box-shadow: 8px 8px 6px 2px #777;
}

.title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.logo {
  width: 32px;
  height: 32px;
  margin-right: 8px;
}
